:root {
  --blackColor: #151515;
  --gradientFonColor: linear-gradient(180deg,
      rgba(196, 196, 196, 0) 25%,
      #bedbb0 92.19%);

  --transition: 0.4s;

  --poppinsFont: 'Poppins';

  --borderRadius8: 8px;

  --fontSize10: 8px;
  --fontSize10: 10px;
  --fontSize12: 12px;
  --fontSize14: 14px;
  --fontSize16: 16px;
  --fontSize18: 18px;
  --fontSize20: 20px;
  --fontSize28: 28px;
  --fontSize40: 40px;

  --fontWeight700: 700;
  --fontWeight600: 600;
  --fontWeight500: 500;
}

#root {
  padding: 0;
}

body {
  background-image: var(--gradientFonColor);
  background-repeat: no-repeat;
  margin: 0;
  width: 100%;
  height: 100vh;
}

body[data-theme='light'] {
  --accentColor: #bedbb0;
  --headerColor: #fcfcfc;
  --sidebarColor: #ffffff;
  --helpColor: #f6f6f7;
  --whiteColor: #161616;
  --lowColor: #8fa1d0;
  --mediumColor: #e09cb5;
  --defaultScreenColor: #e6e6e6;

  --opacityWhite: rgba(255, 255, 255, 0.3);
  --opacityWhite2: rgba(255, 255, 255, 0.5);
  --themeTitle: rgba(22, 22, 22, 0.8);
  --themeBgColor: #FCFCFC;
  --themeBoxShadow: rgba(17, 17, 17, 0.10);
  --themeItem: #161616;

  --activeItemBorder: rgba(246, 246, 247, 1);
  --borderBottomColor: rgba(22, 22, 22, 0.1);
}

body[data-theme='dark'] {
  --accentColor: #bedbb0;
  --headerColor: #161616;
  --sidebarColor: #121212;
  --helpColor: #1f1f1f;
  --whiteColor: #ffffff;
  --lowColor: #8fa1d0;
  --mediumColor: #e09cb5;
  --defaultScreenColor: #e6e6e6;
  --calendarBackground: #1F1F1F;

  
  --opacityWhite: rgba(255, 255, 255, 0.3);
  --opacityWhite2: rgba(255, 255, 255, 0.5);
  --themeTitle: rgba(255, 255, 255, 0.8);
  --themeBgColor: #151515;
  --themeBoxShadow: rgba(17, 17, 17, 0.10);
  --themeItem: rgba(255, 255, 255, 0.5);
  

  --activeItemBorder: rgba(31, 31, 31, 1);
  --borderBottomColor: rgba(255, 255, 255, 0.1);
}

body[data-theme='violet'] {
  --accentColor: #5255bc;
  --headerColor: #ffffff;
  --sidebarColor: #5255bc;
  --helpColor: rgba(236, 237, 253, 0.4);
  --whiteColor: #ffffff;
  --lowColor: #8fa1d0;
  --mediumColor: #e09cb5;
  --defaultScreenColor: #e6e6e6;

  
  --opacityWhite: rgba(255, 255, 255, 0.3);
  --opacityWhite2: rgba(255, 255, 255, 0.5);
  --themeTitle: rgba(22, 22, 22, 0.8);
  --themeBgColor: #FCFCFC;
  --themeBoxShadow: rgba(17, 17, 17, 0.10);
  --themeItem: #161616;

  --activeItemBorder: rgba(255, 255, 255, 0.5);
  --borderBottomColor: rgba(255, 255, 255, 0.1);
}

h1,
h2,
h3,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

* {
  box-sizing: border-box;
}